@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

body * {
font-family: 'Pretendard-Regular';
font-size: 14px;
}

/* 헤더영역 서식 */
.header_user{
  max-width: 1050px;
  width:100%;
  margin: 0 auto;
  height:35px;
}
.header_user ul{
  text-align: end;
}
.header_user ul li{
  display: inline-block;
  width:60px;
  text-align: center;
  line-height: 35px;
  padding:0px 10px;
}

header{
  max-width: 1050px;
  width:100%;
  margin: 0 auto;
  height:63px;
  display: flex;
  justify-content: space-evenly;
}
header a:hover{
  color: rgb(95, 0, 128);
}
h1{
  height:63px;
}
.header_tab a{
  margin-left: 20px;
  font-size: 18px;
  font-weight: normal;
  line-height: 40px;
  letter-spacing: normal;
  color: rgb(181, 181, 181);
  cursor: pointer;
}
.header_tab a:first-child{
  margin-right:20px;
  color: rgb(95, 0, 128);
}
.header_tab img{
  margin-left:8px;
  margin-bottom:10px;
}
.header_search{
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  justify-content: space-between;
  width: 400px;
  height: 48px;
  padding-left: 14px;
  border: 1px solid rgb(95, 0, 128);
  border-radius: 6px;
  background-color: rgb(255, 255, 255);
  box-shadow: rgb(247, 247, 247) 0px 0px 0px 1px inset;
  margin-left:20px;
}
.header_search > input{
  border:none;
  width:350px;
  height:48px;
  outline:none;
}
.header_search > button{
  border:none;
  background:none;
  cursor: pointer;
}
.header_icons{
  width:148px;
  height:50px;
  display:flex;
  justify-content: space-evenly;
  align-items: center;
}
.header_icons button{
  background: none;
  border:none;
  width: 36px;
  height: 36px;
  cursor: pointer;
}
.sticky_wrap{
  width:100%;
  height:56px;
  background: #fff;
  position: sticky;
  top:0px;
  z-index: 9999;
}
.header_bot{
  position:sticky;
  top:0px;
  background: #fff;
  z-index: 9999;
  max-width: 1050px;
  width:100%;
  margin: 0 auto;
  height:56px;
  display: flex;
  justify-content: space-between;
}
.header_bot a{
  font-size: 16px;
  font-weight: 500;
  line-height: 56px;
  letter-spacing: -0.3px;
}
.header_bot a:hover{
  color: rgb(95, 0, 128);
}
.header_cate{
  display:flex;
  align-items: center;
  font-size: 16px;
}
.header_cate li img{
  width: 16px;
  height: 14px;
  margin-right: 14px;
}

.header_nav{
  width:600px;
  display: flex;
  justify-content: space-evenly;
}
.header_nav a{
  font-weight: bold;
}
.header_delivery{
  height: 32px;
  padding: 0px 16px 0px;
  margin-top:10px;
  border-radius: 18px;
  border: 1px solid rgb(238, 238, 238);
  color: rgb(102, 102, 102);
  font-size: 13px;
  line-height: 32px;
  letter-spacing: -0.32px;
}

/* 푸터영역 서식 */
footer{
  width:100%;
  margin-top:50px;
  padding:30px 0px;
  background-color: rgb(247, 247, 247);
}
footer address{
  width:100%;
  max-width: 1050px;
  margin: 0 auto;
  text-align: center;
  color: rgb(153, 153, 153);
}